<template>
  <div id="menu-list" :style="showMenu" @click.stop="userShow=false">
    <div id="buffer">
      <tab-button @childEmit="menuList()" v-show="!$store.state.menuShow" />
      <menu-header>
        <div style="display:flex;align-items:center">
          <user-image @itemClick="itemClick()"/>
          <div v-show="!$store.state.isLogin">
            <a href="javascript:void(0);" @click="logReg(true)"></a> /
            <a href="javascript:void(0);" @click="logReg(false)"></a>
          </div>
          <a href="javascript:void(0);" v-show="$store.state.isLogin">{{username}}</a>
        </div>
        <image-btn :title="''" :imgURL="require('@/assets/favorites.png')" @click.native="toFavo()"/>
      </menu-header>
      <list-box @itemClick="itemClick()" />
    </div>
  </div>
</template>

<script>
import UserImage from "@/components/common/user/UserImage.vue";
import MenuHeader from "@/components/MenuHeader";
import TabButton from "@/components/common/TabButton";
import ListBox from "@/components/ListBox";
import ImageBtn from "@/components/common/ImageBtn.vue";
import {getLocation} from "@/js/map-load.js"
export default {
  data() {
    return {
      isShow: false,
      windowSize:window.matchMedia('(max-width:768px)').matches
    };
  },
  props: {
    username: {
      type: String,
      default: ""
    }
  },
  components: {
    MenuHeader,
    TabButton,
    ListBox,
    UserImage,
    ImageBtn
  },
  methods: {
    menuList() {
      this.$store.commit("setMenuShow");
    },
    
    logReg(bool) {
      this.$emit("toLogReg", bool);
    },
    itemClick() {
      this.$store.commit("setMenuShow");
    },
    toFavo(){
      this.itemClick()
      if(this.$route.path!="/favorites"){
        this.$router.push({path:"/favorites"})
      }
    }
  },
  computed: {
    showMenu() {
      return {
        transform:this.windowSize? (this.$store.state.menuShow ? "translateX(0)" : "translateX(-300px)"):"none"
      };
    }
  },
  beforeMount() {
    getLocation()
  },
  mounted() {
    window.onresize=()=>{
      this.windowSize=window.matchMedia('(max-width:768px)').matches
    }
  }
};
</script>

<style scoped>
#menu-list {
  transition: transform 0.4s ease-in-out;
  height: 100vh;
  float: left;
  color: #747d8c;
  background-color: #f1f2f6;
  box-shadow: 0 0 3px #000;
  display: table;
}

@media all and (max-width: 768px) {
  #menu-list {
    width: 300px;
    position: absolute;
    z-index: 2;
    transform: translateX(-300px);
  }
}
@media all and (min-width: 769px) and (max-width: 992px) {
  #menu-list {
    width: 300px;
  }
}
@media all and (min-width: 993px) and (max-width: 1200px) {
  #menu-list {
    width: 300px;
  }
}
@media all and (min-width: 1201px) {
  #menu-list {
    width: 300px;
  }
}
a {
  text-decoration: underline;
  color: #0984e3;
}
a:active {
  color: #e84118;
}
</style>